<template>
  <v-container>
    <div>
      <span class="text-h5 font-weight-bold">{{ title }}</span>
    </div>
    <div>
      <v-tabs active-class="active-tab" slider-color="black" v-model="tabs">
        <v-tab v-for="tab in tablist" :key="tab.key">{{ tab.name }}</v-tab>
      </v-tabs>

      <v-tabs-items v-model="tabs">
        <v-tab-item v-for="items in itemlist" :key="items.key">
          <div v-for="item in items.list" :key="item.key" class="pa-3 for-div">
            <v-card :to="`/event/list/detail?eno=${item.eno}`">
              <v-img :src="item.eimgurl"></v-img>
              <v-card-title>{{ item.etitle }}</v-card-title>
              <v-card-text
                >{{ new Date(item.estartdate).toLocaleString().substring(0, 12) }} ~
                {{ new Date(item.eenddate).toLocaleString().substring(0, 12) }}</v-card-text
              >
            </v-card>
          </div>
        </v-tab-item>
      </v-tabs-items>
    </div>
  </v-container>
</template>

<script>
import EventApi from "@/apis/event";
export default {
  components: {},
  name: "EventList",
  data: () => ({
    title: "Event",
    tabs: null,
    tablist: [
      { key: 1, name: "진행중" },
      { key: 2, name: "종료" },
    ],
    itemlist: [
      { key: 1, list: [] },
      { key: 2, list: [] },
    ],
  }),
  methods: {
    async getEventList() {
      try {
        const promise = await EventApi.eventlist();
        this.itemlist[0].list = promise.data.progress;
        this.itemlist[1].list = promise.data.end;
        console.log(this.itemlist);
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getEventList();
  },
};
</script>

<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
}
</style>
